import React,{useState,useEffect} from 'react';
import {connect} from 'react-redux';
import{ Grid} from '@material-ui/core';
import SinglePost from './SinglePost';
import CircularProgress from '@material-ui/core/CircularProgress';
import { TitleComponent } from './TitleComponent.jsx';
import {fetchUser} from '../actions/userActions';

import { Alert, AlertTitle } from '@material-ui/lab';

const Saved = (props) => {
    const [SavedPosts, setSavedPosts] = useState(null)
    useEffect( ()=>{
        
       props.fetchuser();
       const data= props.posts.filter((post)=>{
          return props.user.bookmarks.includes(post._id);
       });
       setSavedPosts(data);
 
    },[props])
    
    return (
        <div>
        {props.user && (props.user.bookmarks.length===0 && <Alert className="err-box" severity="error">
        <AlertTitle>No posts found</AlertTitle>
        </Alert>)}
        <TitleComponent title={SavedPosts===null?"Loading...":"Memories - A place to store moments"} />
        <Grid container className="container" >
            <Grid item xs={12} md={12} className="post-grid" alignItems="stretch">
            {  SavedPosts===null ?
               <CircularProgress className="loader"/>:
               <React.Fragment>{
                   SavedPosts.map(post => {
                   return <Grid className="post-grid-item" item xs={12} sm={6} md={4} ><SinglePost key={post._id} post={post}/></Grid>
                   })
                }
                </React.Fragment>
            }
            </Grid>
           
        </Grid>
        
        </div>
    );
};
const mapStateToProps = (state) => ({
    user : state.userReducer.user,
    posts:state.postReducer.posts,
    loading:state.postReducer.loading,
    err:state.postReducer.err
})

const mapDispatchToProps = (dispatch) =>{
   return{ fetchuser:()=>dispatch(fetchUser())}
}

export default connect(mapStateToProps,mapDispatchToProps)(Saved);