import React,{useState} from 'react';
import {newPost,setError} from '../actions/postActions'
import {connect} from 'react-redux';
import axios from '../axios';
import FileBase64 from 'react-file-base64';
import { Alert, AlertTitle } from '@material-ui/lab';
import {useHistory } from 'react-router-dom';
import LinearProgress from '@material-ui/core/LinearProgress';
const Form = (props) => {
    const history=useHistory();
    props.seterror(undefined)
    const [formerror,setformerror]=useState(undefined)
    const [progress, setProgress] = useState(0);
    const [form,setForm] =useState({title:"",author:props.user._id,body:"",image:""});
    const handleChange=(e)=>{
        setForm({...form,[e.target.name]:e.target.value})
        setformerror(undefined)
    }
    const countWords = (msg)=>{
       return msg.split(' ').length;
    }
    const handleSubmit=(e)=>{
        e.preventDefault();
        setProgress(20);
        if(form.title==="" || countWords(form.title)<3){ setformerror("Enter the title of min 3 words") ;return;}
        else if(form.body===""|| countWords(form.body)<10){setformerror("Enter the description of min 10 words") ;return;}
        else if(form.image===""){setformerror("Choose a file to upload");console.log(props.err) ;return;}
        else{
            setProgress(50);
        axios.post('/posts/new',form).then((res)=>{
            setProgress(70)
            if(res.data.success===undefined){props.seterror(res.data.error);return;}
            setProgress(100);
            history.push("/dashboard");
            
            props.newpost(res.data.post);
            setForm({title:"",author:"",body:"",image:""});
            
        })
      }
    }
    const Reset = ()=>{
        setForm({title:"",author:"",body:"",image:""});
    }
    return (
        <div className="Form">
        <LinearProgress variant="determinate" value={progress} />
        <h1>Share memory !</h1>
        {formerror!==undefined && <Alert severity="error">
        <AlertTitle>{formerror}</AlertTitle>
        </Alert>}
            <form onSubmit={handleSubmit} className="form">
                <input type="text" value={form.title} name="title" placeholder="Title"  onChange={handleChange} ></input>
                <textarea type="text" value={form.body} name="body" placeholder="Description"  onChange={handleChange}  maxLength={300} ></textarea>
                <FileBase64 placeholder="File upload" onDone={(img)=>{setformerror(undefined);setForm({...form,image:img})}} className="fileupload" name="image">File</FileBase64>
                <div><button className="custombutton" type="submit">Post</button></div>
                <div><button className="custombutton-red" type="submit" onClick={Reset}>Clear</button></div>
            </form>
        </div>
    );
};
const mapStateToProps = (state) => ({
    isLoggedIn:state.userReducer.isLogged,
    user:state.userReducer.user,
    err:state.postReducer.err
})

const mapDispatchToProps = (dispatch)=>{
    return {
        newpost : (payload)=>dispatch(newPost(payload)),
        seterror : (payload)=>dispatch(setError(payload))
    }
};

export default  connect(mapStateToProps,mapDispatchToProps)(Form);