import React,{useState} from 'react';
import {signIn} from '../actions/userActions'
import Login from './Login' ;
import Signup from './Signup';
import {connect} from 'react-redux';
import{ Grid,Paper} from '@material-ui/core';
import img from './logo.png'

const Auth = (props) => {
    const [login,openLogin]=useState(true);
    return (
        <div className="Auth">
        <div><img src={img} alt="logo" className="memories"></img></div>
        <h1>Welcome to Memories</h1>
        <Paper elevation={3} className="Paper">
        <Grid container className="container" >
        
            <Grid item xs={12} md={8}>
                {login ?<Login setOpen={openLogin} />: <Signup setOpen={openLogin}/>}
            </Grid>
            <div className="vl-outer">
            <div className="vl">
            <span className="vl-innertext">or</span>
            </div>
        </div>
            <Grid item xs={12} md={4} className="right">
            <a href='/auth/google' className="google-btn">
            <div className="google-icon-wrapper">
              <img alt="google" className="google-icon" src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg"/>
            </div>
            <p className="btn-text"><b>Sign in with google</b></p>
          </a>
            
            </Grid>
        </Grid>
        </Paper>

        </div>
    );
};
const mapDispatchToProps = (dispatch)=> {
    return {
    login : (payload)=>dispatch(signIn(payload))
    }
}

export default connect(null,mapDispatchToProps)(Auth);