import './App.css';
import React,{useEffect} from 'react';
import Auth from './Auth';
import Navbar from './Navbar';
import Dashboard from './Dashboard';
import {connect} from 'react-redux';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Mine from './Mine.js';
import Saved from './Saved.js';
import PostForm from './PostForm.js'
import {fetchUser} from '../actions/userActions'
import CircularProgress from '@material-ui/core/CircularProgress';

function App(props) {
  useEffect(() => {
    props.fetchuser();
}, [props])
  return (
    <div className="App">
        <Router>
        <div>
        {props.loaduser ? <CircularProgress className="loader"/>
          :<Switch>
            <Route path="/" exact> {props.oggedIn? <Redirect to="/dashboard" exact/>:<Redirect to="/Auth"/>}</Route>
            <Route path="/dashboard" exact>
               {props.loggedIn ?<React.Fragment><Navbar/><Dashboard posts={props.posts}/></React.Fragment>:<Redirect to="/Auth" exact/>}
            </Route>
            <Route path="/newPost" exact>
               {props.loggedIn ?<React.Fragment><Navbar/><PostForm/></React.Fragment>:<Redirect to="/Auth" exact/>}
            </Route>
            <Route path="/myposts" exact>
               {props.loggedIn ?<React.Fragment><Navbar/><Mine posts={props.posts} /></React.Fragment>:<Redirect to="/Auth" exact/>}
            </Route>
            <Route path="/saved" exact>
               {props.loggedIn ?<React.Fragment><Navbar/><Saved posts={props.posts}/></React.Fragment>:<Redirect to="/Auth" exact/>}
            </Route>
            <Route path="/Auth" exact>
            {props.loggedIn? <Redirect to="/dashboard" exact/>:<Auth/> }
            </Route>
          </Switch>}
        </div>
      </Router>
    </div>
  );
}

const mapStateToProps = (state) => ({
  posts:state.postReducer.posts,
  loggedIn : state.userReducer.isLogged,
  loaduser:state.userReducer.loaduser
})
const mapDispatchToProps = (dispatch) =>{
  return{fetchuser:()=>dispatch(fetchUser())}
}


export default connect(mapStateToProps,mapDispatchToProps)(App);

